import React from "react";
import { withRouter, Link } from "react-router-dom";

import { transition } from "../../components/Transition";

import Header from "../../components/Header";
import Button from "../../components/Button";

import Mountains from "../../assets/images/landing-mountains.png";
import Car from "../../assets/images/landing-car.png";
import Chargers from "../../assets/images/nabijacky-1.png";
import Network from "../../assets/images/business.png";
import Logo from "../../assets/images/logo.png";

import ChargerCarMountains1 from "../../assets/images/charger-car-mountains-1.png";
import ChargerCarMountains2 from "../../assets/images/charger-car-mountains-2.png";

import "./landing.css";

class Landing extends React.Component {

    componentDidMount() {
        document.addEventListener('DOMContentLoaded', function() {
            var parent = document.querySelector('.splitview'),
                topPanel = parent.querySelector('.top'),
                handle = parent.querySelector('.handle'),
                skewHack = 0,
                delta = 0;
        
            // If the parent has .skewed class, set the skewHack var.
            if (parent.className.indexOf('skewed') != -1) {
                skewHack = 1000;
            }
        
            parent.addEventListener('mousemove', function(event) {
                // Get the delta between the mouse position and center point.
                delta = (event.clientX - window.innerWidth / 2) * 0.5;
        
                // Move the handle.
                handle.style.left = event.clientX + delta + 'px';
        
                // Adjust the top panel width.
                topPanel.style.width = event.clientX + skewHack + delta + 'px';
            });
        });
    }

    render() {
        return(
            <div id="main-landing">
                <Header />

                <div class="splitview skewed">
                    <div class="slider-panel bottom">
                        <div class="content">
                            <img src={ChargerCarMountains1} alt="Original" />
                        </div>
                    </div>

                    <div class="slider-panel top">
                        <div class="content">
                            <img src={ChargerCarMountains2} alt="Duotone" />
                        </div>
                    </div>

                    <div class="handle"></div>
                </div>

                {/*
                <div className="section section-1">
                    <img className="image" src={Mountains} />

                    <img className="logo" src={Logo} />
                    <div className="title">We understand the future</div>
                </div>
                */}

                <div className="section section-2">
                    <div className="panel-left">
                        <div className="heading">
                            Nabíjajte svoje vozidlo rýchlo a pohodlne
                        </div>

                        <div className="text">
                            Naše nabíjacie stanice Vám zaručia rýchle a kvalitné nabíjanie priamo v pohodlí domova. K tomu sú šetrné ku životnému prostrediu a disponujú jednoduchým a intuitívnym pouźívateľským rozhraním.
                        </div>
                        
                        <Button
                            title="Zisti viac"
                            onClick={() => {}}
                            black
                        />
                    </div>

                    <div className="panel-right">
                        <img className="image" src={Chargers} />
                    </div>
                </div>

                <div className="section section-3">
                    <div className="panel-left">
                        <img className="image" src={Network} />
                    </div>

                    <div className="panel-right">
                        <div className="heading">
                            Ste na cestách? S našou sieťou nabíjacích staníc žiaden problém
                        </div>

                        <div className="text">
                            Disponujeme stovkami nabíjacích staníc rozložených po celom Slovensku. Ak sa Vám vybije auto, otvorte si našu mapu nabíjacích staníc a vyskúšajte si ich na vlastnú kožu.
                        </div>
                        
                        <Button
                            title="Zisti viac"
                            onClick={() => {}}
                            black
                        />
                    </div>
                </div>

                <div className="section section-4">
                    <div className="panel left" onClick={() => transition(() => this.props.history.push("/produkty"))}>
                        <img className="mountains" src={Mountains} />
                        <img className="car" src={Car} />

                        <div className="content">
                            <div className="title">Nabíjacie stanice</div>
                        </div>
                    </div>

                    <Link className="panel right">
                        <div className="content">
                            <div className="title">Sieť staníc</div>
                        </div>
                    </Link>
                </div>
            </div>
        )
    }
}

export default withRouter(Landing);